.__mottled_networkicon_container {
  display: flex;
  align-items: center;
}

._mottled_icon {
  font-family: "Web3-Regular";
  text-align: center;
}

.cap {
  text-transform: capitalize;
}

.txt-xs {
  font-size: 0.75rem;
  line-height: 1rem;
}
.txt-sm {
  font-size: 0.875rem;
  line-height: 1.25rem;
}
.txt-base {
  font-size: 1rem;
  line-height: 1.5rem;
}
.txt-xl {
  font-size: 1.25rem;
  line-height: 1.75rem;
}
.txt-2xl {
  font-size: 1.5rem;
  line-height: 2rem;
}
.txt-3xl {
  font-size: 1.875rem;
  line-height: 2.25rem;
}
.txt-4xl {
  font-size: 2.25rem;
  line-height: 2.5rem;
}
.txt-5xl {
  font-size: 3rem;
  line-height: 1;
}
.txt-6xl {
  font-size: 3.75rem;
  line-height: 1;
}
.txt-7xl {
  font-size: 4.5rem;
  line-height: 1;
}
.txt-8xl {
  font-size: 6rem;
  line-height: 1;
}
.txt-9xl {
  font-size: 8rem;
  line-height: 1;
}
